*{
  margin: 0;
  padding: 0;
}
/* header------------------------------------------------------------ */
.header{
  display: flex;
  align-items: center;
 background: #000000;
 padding: 5px 5px;
 position: sticky;
  top: 0;
  z-index: 100;

}
.header-title{
  flex: 1;
  padding: 5px 10px;
}
.header-title a{
font-family: 'Russo One', sans-serif;
  display: flex;
  align-items: center;
  text-decoration: none;
  font-size: 24px;
  color: rgb(255, 255, 255);
  width: fit-content;
}
.header-title a:hover{
  filter: brightness(0.8);
}
.header-title a img{
  width: 30px;
  object-fit: contain;
  margin: 0px 5px;

}
.header ul{
  align-items: center;
  display: flex;
  list-style: none;
  gap: 20px;
  padding: 5px 50px
}
.header ul li a.active {
  border-bottom: 2px solid rgb(154, 226, 11);
}
.header ul li a{
  font-family: 'Roboto', sans-serif;
  text-decoration: none;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  font-size: 17px;
  padding: 4px 8px;
}
.header ul li a:hover{
  background: #663333;
  border-radius: 5px;
}
.dropdownmenu label a:hover{
  background: #663333;
  border-radius: 5px;
  
}
.dropdownmenu label a:hover #flip{
  rotate: 180deg;
}
.dropdownmenu label a{
  display: flex;
  align-items: center;
font-family: 'Roboto', sans-serif;
  text-decoration: none;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  font-size: 17px;
}
.dropdownmenu #flip{
  font-weight: bold;
    font-size: 30px;
    padding: 0;
    margin: 0;
    transition: all 0.3s ease-in-out;
}
.dropdownmenu{
  position: relative;
  display: inline-block;
}
.dropdownitem{
  display: none;
  position: absolute;
  padding: 5px 2px;
  background: #ffffff;
  border-radius: 5px;
}
.dropdownmenu:hover .dropdownitem{
 display: block;
}
.dropdownitem  a{
  font-family: 'Agdasima', sans-serif;
  color: #000;
  margin: 5px 0px;
  padding: 2px 10px;
  display: block;
  width: 150px;
  text-decoration: none;
  font-size: 18px;
  letter-spacing: 3px;
  border-radius: 5px;
}
.dropdownitem a:hover{
  font-weight: bold;
  background: rgb(177, 207, 217);
}



/* signup--------------------------------------------------------- */
.signup{
  width: 350px;
  padding: 10px;
  margin: auto;
}
.signup h3{
  font-size: 25px;
    font-family: math;
    letter-spacing: 5px;
    text-align: center;
    margin: 5px;
}
.signup form{
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.signup form input{
  padding: 5px;
  font-size: 16px;
  outline: none;
}
.signup form button{
  padding: 5px;
  font-size: 16px;
  letter-spacing: 5px;
  text-transform: uppercase;
  outline: none;
  cursor: pointer;
}
/* login--------------------------------------------------------------- */
.login{
  width: 350px;
  padding: 10px;
  margin: auto;
}
.login h3{
  font-size: 25px;
    font-family: math;
    letter-spacing: 5px;
    text-align: center;
    margin: 5px;
}
.login form{
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.login form >p{
  font-family: 'Plus Jakarta Sans', sans-serif;
    font-size: 14px;
    color: darkred;
    font-weight: bold;
    cursor: pointer;
}
.login form input{
  padding: 5px;
  font-size: 16px;
  outline: none;
}
.login form button{
  padding: 5px;
  font-size: 16px;
  letter-spacing: 5px;
  text-transform: uppercase;
  outline: none;
  cursor: pointer;
}

/* Adminmenu------------------------------------------------------------ */
.adminmenu{
 padding: 10px;
}
.adminmenu h2{
  font-family: sans-serif;
  letter-spacing: 3px;
