}

/* createcategory------------------------------------------------ */
.createcategory{
  padding: 10px;

}
.createcategory h2{
  font-family: sans-serif;
  letter-spacing: 3px;
  margin: 10px 0px;
  font-size: 25px;
}

.createcategory form button{
  display: block;
    padding: 6px 15px;
    outline: none;
    border: none;
    background: black;
    color: white;
    border-radius: 5px;
    cursor: pointer;
