  border-bottom: 2px solid black;
}
.category-cont div:first-child span{
  font-size: 22px;
  letter-spacing: 4px;
  font-family: math;
  font-weight: bold;
}
.category-cont > div{
  display: flex;
  justify-content: space-between;
  padding: 5px;
