}
.category-item h5{
  font-size: 19px;
  font-weight: 500;
  font-family: serif;
} 
.category-item div{
 display: flex;
 gap: 10px;
}
.category-item div button{
  padding: 5px 12px;
    cursor: pointer;
    font-size: 15px;
    background: black;
    color: white;
    outline: none;
    border: none;
    border-radius: 5px;
}
.updatecategory-modal{
     position: absolute;
    top: 50%;
    background: #68ca9c;
    padding: 20px 40px;
}
.updatecategory-modal input{
  padding: 5px;
  border: none;
  font-size: 15px;
  margin: 15px 0px;
}
.updatecategory-modal div{
  display: flex;
  justify-content: space-between;
}
.updatecategory-modal div button{
  padding: 5px 12px;
  border: none;
  outline: none;
  border-radius: 4px;
