    height: 40px;
    gap: 0px;
  }
  .admincontrolui{
    display: flex;
    flex-direction: column;
  }
  .usercontrolui {
    display: flex;
    flex-direction: column;
  }
  .signup {
    width: 90vw;
    margin: 10px auto;
  }
  .order-information {
    padding: 20px 10px;
}
.order-information-table{
  display: flex;
}
.order-information-table > tr{
  display: flex;
  flex-direction: column;
}
.order-information-table td {
  font-size: 12px;
  padding: 8.5px 20px;
  background: #fbe8e8;
}
.order-information-table tr{
  display: flex;
  flex-direction: column;
}
.productdetails {
  display: flex;
  margin: 20px 0px;
  justify-content: center;
}
.cartpage-cont-checkout {
  display: flex;
  flex-direction: column;
}
.cartpage-checkout{
  padding: 10px 20px;
}
.maincont{
  margin-top:50px;
}
}
@media only screen and (max-width:500px) {
  .productdetails-image {
    width: 40vw;
  }
  .productdetails-info {
    width: 40vw;
  }
  .homepage{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .searchinput input{
    width: 50vw;
  }
  .homepage-products-item {
    width: 75vw;
  }
  .homepage-product {
    margin-top: 0px;
  }
  .products-card {
    width: 75vw;
  }
  .userdashboard, .usercontrolui {
    display: flex;
    flex-direction: column;
}
.login {
  width: 90vw;
}
.sentotpcont button {
  margin: 10px 5px;
}
}
@media only screen and (max-width:375px) {
  .header ul li a{
    font-size: 15px;
    padding: 4px 5px;
  }  
  .dropdownmenu label a {
    font-size: 16px;
  }
}